import PubElements from "../pubElements/PubElements"
export default {
  name: 'rSlider',
  components: {
    PubElements
  },
  props: {
    amount: {
      type: Number,
      default: 3
    }
  },
  data () {
    return {
      position: '0',
      nbrRow: 1,
      aName: 'example',
      transition: 'left 0.3s',
      startPoint: 0,
      prevPositon: 0,
      min: -200,
      offset:93,

      //----- data related variables---------------------
      elements:[{title:'Film publicitaire', description:'Spot publicitaire/ Film entreprise',image:'s4.png'},
                {title:'Photographie', description:'Lorem ipsum dolor sit amet concetetur discipin elit',image:'s8.png'},                    
                {title:'Film corporate', description:'Film entreprise/ Film institution/ Reportage d\'activité video/ Communication interne/ Video de voeux',image:'s2.png'},
                {title:'Interview reportage', description:'Temoignage/ Film portrait/ Publireportage',image:'s6.png'},
                {title:'Courverture', description:'',image:'s1.png'},
                {title:'Motion Design', description:'Video 3D/ Infographie video/ Stop motion',image:'s4.png'},
                {title:'Caption live', description:'Conference de presse / Assemblee/ Colloque de cérémonie/ Formation webinaire/ Forum virtuel interview/ Master class/ Concert',image:'s8.png'},
                {title:'Sonorisation', description:'Spectacle live/ Video conference/ Teleconference/ Livestreaming/ Live sur grad écran',image:'s2.png'},
                {title:'Film produit', description:'Presentation application/ Site web/ Produit/ Service/ Testimoniaux',image:'s6.png'},
                {title:'Location', description:'Location photo/ Location video/Location materielle ausiovisuelle/ Location camera/Location lumiere',image:'s4.png'},
                {title:'Webserie Brand content', description:'Webserie',image:'s8.png'},
                {title:'Impression', description:'Impression offset/ Impression numérique/ Grand format/ Serigraphie et broderie/ Marquage sur tout support',image:'s2.png'},
                {title:'Video E-learning', description:'Webserie/ Video de formation/ Animation pédagogique/ Film de vulgarisation',image:'s6.png'},
                {title:'Developpement /Web-design', description:'Conception maquette/ Developpement d\'application web/ Developpement de site web',image:'s4.png'},
                {title:'Infographie/ Graphisme', description:'Banderolle/ Rolle-up/ T-shirt/ Flyers/ Agenda/ Calendrier/ Poster géant/ carte de visite',image:'s8.png'},
                {title:'Communication digitale', description:'',image:'s2.png'}
      ]
    }
  },
  computed: {
    Position () {
      return this.position+'%'
    }
  },
  methods: {
    dragStart (event) {
      if(this.amount > 2 ) event.preventDefault()
      this.startPoint = event.type == 'touchstart' ? event.touches[0].pageX : event.pageX
      this.prevPositon = this.position
    },
    dragging (event) {
      if(this.startPoint != 0) {
        let offset = event.type == 'touchmove' ? event.touches[0].pageX : event.pageX
        //alert(offset)
        //------------------------------------------------------------
        let a = offset - this.startPoint
        a = a/window.innerWidth*60
        let newPosition = parseFloat(this.position) + a
        if(newPosition >= this.min && newPosition <= 0) this.position = newPosition
      }
    },
    dragEnd () {
      let dragDistance = this.prevPositon - this.position
      //------ multiplicateur --------------------------
      let m = 1
      if(Math.abs(dragDistance) > 140 ) m = 2
      else if(Math.abs(dragDistance) > 200 ) m = 3
      else if(Math.abs(dragDistance) > 230 ) m = 4

      let r = this.prevPositon
      if( dragDistance < 0 ) r = this.prevPositon + m*this.offset
      else if(dragDistance > 0) r = this.prevPositon - m*this.offset

      if(r <= this.min) this.position = this.min
      else if(r >= 0) this.position = 0
      else this.position = r - r%this.offset
      this.startPoint = 0
    }
  },
  mounted () {
    
    let a = this.elements.length / this.amount
    this.nbrRow = Array(Math.ceil(a))
    this.min = -93*(Math.ceil(a)-1)
    this.offset = 93/this.amount
  }
}
