import LLoading from '@/components/lLoading/LLoading'
export default {
  name: 'lPblicationForm',
  components: {
    LLoading
  },
  data () {
    return {
      Display: 'none',
      isDisplayed: false,
      traduction: ['Armand','jean'],
      ngomala: 'Chieu',
      yesNoVisible: false,
      mode: 'add',
      loading: false
    }
  },
  methods: {
    displaying (mode,word) {
      this.Display = this.isDisplayed ? 'none' : 'block'
      this.isDisplayed = !this.isDisplayed
      this.mode = mode
      this.ngomala = word
      if(word == '') {
        let i
        for(i = 0 ; i<10; i++) { this.traduction[i] = ''}
      }
      else this.getTraduction()
    },
    saving () {
      if(this.mode ==='add' && this.wordExist(this.ngomala)) {
        this.yesNoVisible = true
      }
      else { this.setWord() }
    },
    yesEdit () {
        this.getTraduction()
        this.mode = 'modify'
        this.yesNoVisible = false
    },
    setWord () {
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'addWord.php',{
        ngomala: this.ngomala,
        franci: this.traduction,
        length: this.traduction.length
      })
        .then((response) => {
          console.log(response.data)
          this.$store.commit('getWords',response.data[0])
          this.$store.commit('getWordsFranci',response.data[1])
          this.$emit('wordSet')
          this.displaying()
          this.loading = false
        })
        .catch((error) => {
          console.log(error)
          this.loading = false
        })
    },
    wordExist (word) {
      let i, max = this.$store.state.words.length
      let words = this.$store.state.words
      for(i=0; i<max; i++) {
        // alert(words[i].ngomala)
          if(words[i].ngomala === word) {             
            return true
          }
      }
      return false
    },
    getTraduction () {
      let i, max = this.$store.state.words.length
      let words = this.$store.state.words
      this.traduction = []
      for(i=0; i<max; i++) {
          if(words[i].ngomala === this.ngomala) {
              this.traduction.push(words[i].franci)              
          }
      }
      this.enoughCase(this.traduction)
    },
    enoughCase (tab) {
      if(tab.length < 10) {
        let i
        let next = 10-tab.length
        for(i = 0; i<next; i++) {
          tab.push('')
        }
      }
    }
  },
  mounted () {
    this.enoughCase(this.traduction)
  }
}
